:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

.search-container {
  background-color: #3232b3;
  margin-left: auto;
  margin-right: auto;
}

.search-form {
  text-align: center;
  padding: 10px 0;
}

.search-input {
  height: 35px;
  width: 300px;
  border-color: #3232b3;
  border-radius: 4px;
}

.search-btn {
  color: #3232b3;
  background-color: #fff;
  border-color: #3232b3;
  border-radius: 4px;
  margin: 15px auto;
  padding: 5px 10px;
  font-size: large;
  font-weight: 400;
}

.gallery {
  width: 1200px;
  flex-wrap: wrap;
  gap: 20px;
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
  display: flex;
}

.photo-card {
  flex-basis: calc(25% - 15px);
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.gallery__image:hover {
  transform: scale(1.03);
}

.gallery__image {
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.gallery__link:hover {
  cursor: zoom-in;
}

.info {
  text-align: center;
  border: 1px solid #eee;
  border-top: none;
  justify-content: space-between;
  display: flex;
}

.button {
  color: #fafafa;
  background-color: #3232b3;
  border-color: #3232b3;
  border-radius: 4px;
  margin: 15px auto;
  padding: 10px;
  font-size: large;
  font-weight: 400;
  display: block;
}

.button.is-hidden, .button > .spinner.is-hidden {
  display: none;
}

.spinner-border-sm {
  width: .5rem;
  height: .5rem;
  border-width: .2em;
}

.spinner-border {
  width: 1.2rem;
  height: 1.2rem;
  vertical-align: text-bottom;
  border: .25em solid;
  border-right-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: spinner-border .75s linear infinite;
  display: inline-block;
}

@keyframes spinner-border {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes spinner-grow {
  0% {
    transform: scale(0);
  }

  50% {
    opacity: 1;
    transform: none;
  }
}

